var ametric = (am = {}) => {  // base structure for metrics
	if (!am) { am = {} }
	return {
		rev: am.rev || 0,
		leads: am.leads || 0,
		gleads: am.gleads || 0,
		wins: am.wins || 0,
		close: am.close || 0,
		rpo: am.rpo || 0,
		cancels: am.cancels || 0,
		opts: am.opts || {}
	}
}

/**
 * This function creates metrics based on a given list of
 * tracking docs. It will return a metric structure to be
 * used.
 * 
 * @param { object[] } list The list of items to be tallied
 * @param { object } rsets Residential Settings
 * @param { number | string | null } [ testyear = null ] The year the metrics should be calculated in reference to
 * @param { boolean } [ co = false ] Are we calculating strictly the Carry Over items?
 * @returns { object } Metrics object
 */
export var GENmetrics = (list, rsets, testyear = null, co = false) => {  // Generates base metrics
	let categories = rsets.cat;
	let metrics = ametric();
	let qwinlose = {};

	for (let c in categories) { //sum categories
		metrics.opts[c] = {
			total: 0,
			leads: 0,
			wins: 0,
			winper: 0,
			rpo: 0
		}
	}

	if (testyear == null) {
		testyear = Number(new Date().getFullYear())
	} else {
		testyear = Number(testyear)
	}
	let testdate = 0;
	for (let x = 0; x < list.length; x++) {
		if (list[x].status.toLowerCase() != 'cancelled' && list[x].status.toLowerCase() != 'canceled') {
			testdate = Number(new Date(list[x].date).getFullYear());
			if (!co) {
				if (!qwinlose[list[x].id] && testdate == testyear) {  // create win/loss track if it does not exist (only if it is within the test year)
					qwinlose[list[x].id] = {
						win: false,
						co: false
					};
				} else if (!qwinlose[list[x].id] && testdate < testyear) {  // falls into Carry Over
					qwinlose[list[x].id] = {
						win: false,
						co: true
					};
				}
			} else {
				if (!qwinlose[list[x].id]) {  // create win/loss track if it does not exist
					qwinlose[list[x].id] = {
						win: false,
						co: false
					};
				}
			}

			for (let b = 0; b < list[x].bids.length; b++) {
				if (list[x].bids[b].cat && list[x].bids[b].cat != '') {
					if (!metrics.opts[list[x].bids[b].cat]) {
						console.log('NOT A VALID CATEGORY >> ', list[x].bids[b].cat, list[x].estimator)
						metrics.opts[list[x].bids[b].cat] = {
							total: 0,
							leads: 1,
							wins: 0,
							winper: 0,
							rpo: 0,
							warning: 'NOT A VALID CATEGORY'
						}
					} else if (qwinlose[list[x].id].co == false) {
						metrics.opts[list[x].bids[b].cat].leads++;
					}
				}
				if (list[x].bids[b].sold) {
					metrics.rev = metrics.rev + Number(list[x].bids[b].amount);
					if (list[x].bids[b].cat && list[x].bids[b].cat != '') {
						if (metrics.opts[list[x].bids[b].cat]) {
							metrics.opts[list[x].bids[b].cat].wins++;
							metrics.opts[list[x].bids[b].cat].total += Number(list[x].bids[b].amount);
						}
					}
				}
			}
		} else { metrics.cancels++ }
	}

	for (let wl in qwinlose) {  // tally wins and loses
		if (qwinlose[wl].co == false) {
			metrics.leads++;
		}
		if (qwinlose[wl].win == true) {
			metrics.wins++;
		}
	}

	metrics.gleads = metrics.leads + metrics.cancels;  // gross leads (before cancels)(still excludes CO leads)

	for (let c in metrics.opts) {
		if (metrics.opts[c].leads != 0) {  // calculates RPO and win %
			metrics.opts[c].rpo = Number((metrics.opts[c].total / metrics.opts[c].leads).toFixed(2));
			metrics.opts[c].winper = Number(((metrics.opts[c].wins / metrics.opts[c].leads) * 100).toFixed(1));
		}
	}

	if (metrics.leads != 0) {  // calculates RPO and close %
		metrics.rpo = Number((metrics.rev / metrics.leads).toFixed(2));
		metrics.close = Number(((metrics.wins / metrics.leads) * 100).toFixed(1));
	}

	return metrics;
}

/**
 * This function is used to break out the zip code data.
 * It should return a table of all zip code break-outs
 * 
 * @param { object[] } list The list of items to be sorted
 * @returns { object[] } Zip code table
 */
export var CREATEplaces = (list) => {
	let ziplist = {};
	for (let x = 0; x < list.length; x++) {
		let zip = list[x].zip.trim()
		if (!ziplist[zip]) {
			ziplist[zip] = {
				leads: 0,
				wins: 0,
				total: 0
			}
		}
		ziplist[zip].leads++;
		let winner = false;
		for (let i = 0; i < list[x].bids.length; i++) {
			if (list[x].bids[i].sold) {
				winner = true;
				ziplist[zip].total += Number(list[x].bids[i].amount);
			}
		}
		if (winner) { ziplist[zip].wins++ }
	}

	list = [];
	for (let zip in ziplist) {  // Put into array to make sortable
		list.push({
			zip: zip,
			leads: ziplist[zip].leads,
			wins: ziplist[zip].wins,
			winper: ((ziplist[zip].wins / ziplist[zip].leads) * 100).toFixed(1),
			total: (ziplist[zip].total).toFixed(2),
			rpo: (ziplist[zip].leads != 0 ? ziplist[zip].total / ziplist[zip].leads : 0).toFixed(2)
		})
	}

	return list;
}

/**
 * Creates a table-base summary for display
 * @param { object[] } list The list of items to be sorted
 * @param { object } rsets Residential Settings to establish options lists
 * @returns 
 */
export var GENsourcetables = (list, rsets) => {
	let leadlist = {};
	let leadtable = [];
	let sourcelist = {};
	let sourcetable = [];
	let reflist = rsets.source;

	for (let ea in reflist) {
		leadlist[ea] = { leads: 0, wins: 0, total: 0 };
		sourcelist[ea] = { leads: 0, wins: 0, total: 0 };
	}

	for (let x = 0; x < list.length; x++) {
		let lead = list[x].lead;
		let source = list[x].source;

		if (lead != undefined && leadlist[lead]) { leadlist[lead].leads++; }
		if (source != undefined && sourcelist[source]) { sourcelist[source].leads++; }

		let winner = false;
		for (let i = 0; i < list[x].bids.length; i++) {
			if (list[x].bids[i].sold) {
				winner = true;
				if (lead != undefined && leadlist[lead]) { leadlist[lead].total += Number(list[x].bids[i].amount) }
				if (source != undefined && sourcelist[source]) { sourcelist[source].total += Number(list[x].bids[i].amount) }
			}
		}
		if (winner) {
			if (lead != undefined && leadlist[lead]) { leadlist[lead].wins++; }
			if (source != undefined && sourcelist[source]) { sourcelist[source].wins++; }
		}
	}

	for (let ea in leadlist) {
		leadtable.push({
			lead: ea,
			leads: leadlist[ea].leads,
			wins: leadlist[ea].wins,
			winper: Number(leadlist[ea].leads != 0 ? ((leadlist[ea].wins / leadlist[ea].leads) * 100).toFixed(2) : 0),
			total: Number((leadlist[ea].total).toFixed(2)),
			rpo: Number((leadlist[ea].leads != 0 ? leadlist[ea].total / leadlist[ea].leads : 0).toFixed(2))
		});
		sourcetable.push({
			source: ea,
			leads: sourcelist[ea].leads,
			wins: sourcelist[ea].wins,
			winper: Number(sourcelist[ea].leads != 0 ? ((sourcelist[ea].wins / sourcelist[ea].leads) * 100).toFixed(2) : 0),
			total: Number((sourcelist[ea].total).toFixed(2)),
			rpo: Number((sourcelist[ea].leads != 0 ? sourcelist[ea].total / sourcelist[ea].leads : 0).toFixed(2))
		})
	}

	return { lead: leadtable, source: sourcetable }
}