export function toTitleCase(str) {
    var lcStr = str.toLowerCase();
    return lcStr.replace(/(?:^|\s)\w/g, function(match) {
    return match.toUpperCase();
    });
}

export function SORTlist(list,sortopt,sortorder){
    return list.sort((a,b)=>{
		const itemA = a[sortopt]; 
		const itemB = b[sortopt]; 
		if (itemA < itemB) {
			return -1*sortorder;
		}
		if (itemA > itemB) {
			return 1*sortorder;
		}
		return 0;
    });
}

export function SORTtracker(list,sortorder, sortopt){
	let key = Object.keys(sortopt)[0]
	if (key) {
		return list.sort((a,b)=>{
			const itemA = a[key]; 
			const itemB = b[key];
			if (itemA != null && itemB != null) {
				if (itemA < itemB) {
					return -1*sortorder;
				}
				if (itemA > itemB) {
					return 1*sortorder;
				}
			}
			return 0;
		});
	} else {
		return list
	}
}

export function priceformat(price,cents=true){
  let fprice = new Intl.NumberFormat(`en-US`, {
      currency: `USD`,
      style: 'currency',
    }).format(price);
  return cents?fprice:fprice.split('.')[0];  
}

export function dateformat(date) {
	return date.split('T')[0];
}


export function deepEqual(object1, object2) {
  const keys1 = Object.keys(object1);
  const keys2 = Object.keys(object2);
  if (keys1.length !== keys2.length) {
    return false;
  }
  for (const key of keys1) {
    const val1 = object1[key];
    const val2 = object2[key];
    const areObjects = isObject(val1) && isObject(val2);
    if (
      areObjects && !deepEqual(val1, val2) ||
      !areObjects && val1 !== val2
    ) {
      return false;
    }
  }
  return true;
}
function isObject(object) {
  return object != null && typeof object === 'object';
}


// Need to check for leading characters
// Need to send error messege
export function stringValidate(str){
  let spChar = "/[!@#$%^&*()_+\-=\[\]{};':\\|,.<>\/?]+/";
  for (var i = 0; i < str.length; i++) {
      if (spChar.indexOf(str.charAt(i)) != -1) {
          return true;
      }
  }
  return false;
}

export function formatter(data) {
	return `${(data.percent * 100).toFixed(0)}%`
}