import React, { Component } from 'react';
import { Clone, FloatContainer, BasicHeader, AppContainer, MenuSideBar, GetMenuCollapsed, SetCollapsibleTab, GoHome } from 'vhp-component-library';
import { Core } from 'vhp-api';
import { TrackerView } from './Tracker/TrackerView';
import { AnalyticsView } from './Analytics/AnalyticsView';
import { TrackerEditor } from './TrackerEditor';
import { CalculateBidAmount } from '../bin/tracker-tools';


/**
{
	id:"CommissionView",
	title:"Commissions View"
},
 */
let apps = [
	{
		id:"TrackerView",
		title:"Leads"
	},
	{
		id:"AnalyticsView",
		title:"Analytics View"
	}
]

let tabs = ['Leads', 'Analytics']

if (window.ipcRenderer != undefined) {
	ipcRenderer.on('page-close',(eve,data)=>{ipcRenderer.invoke('home',{})})
}

/**
 */
export class ResidentialTracker extends Component {
    constructor(props) {
        super(props)

        this.state = {
            currentTab:0,
			quotes:[],
			leadOverviewActive:false,
			selectedLead:null,
			collapsed:"",
			settings:{}                                                            
			//metrics:null
        }
        
		this.GetPage = this.GetPage.bind(this)
		this.ToggleLeadOverview = this.ToggleLeadOverview.bind(this)
		this.AddBid = this.AddBid.bind(this)
		this.EditBid = this.EditBid.bind(this)
		this.RemoveBid = this.RemoveBid.bind(this)
		this.GetMenuCollapsed = GetMenuCollapsed.bind(this)
		this.SetTab = SetCollapsibleTab.bind(this)
    }

	AddBid(data) {
		let quotes = Clone(this.props.quotes)
		for (let i = 0; i < quotes.length; i++) {
			if (quotes[i]._id == data.id) {
				quotes[i].bids.push(data.bid)
				break;
			}
		}

		this.props.SetQuotes(quotes)
	}

	RemoveBid(data) {
		let quotes = Clone(this.props.quotes)
		for (let i = 0; i < quotes.length; i++) {
			if (quotes[i]._id == data._id) {
				quotes[i].bids.splice(data.index, 1)
				break;
			}
		}

		this.props.SetQuotes(quotes)
	}

	EditBid(value, data) {
		console.log(value, data)
		let quotes = Clone(this.props.quotes)
		for (let i = 0; i < quotes.length; i++) {
			if (quotes[i]._id == data._id) {
				if (data.key == "sold") {
					quotes[i].bids[data.index].sold = !quotes[i].bids[data.index].sold
				} else if (data.key == "cat"|| data.key == "saletype"|| data.key == "progress") {
					quotes[i].bids[data.index][data.key] = value.value
				} else if (data.key == 'amount') {
					if (!isNaN(CalculateBidAmount(value))) {
						quotes[i].bids[data.index][data.key] = CalculateBidAmount(value)
					}
					for (let key in value) {
						if (value[key] == undefined) {
							value[key] = ""
						}
						quotes[i].bids[data.index][key] = value[key]
					}
				} else {
					quotes[i].bids[data.index][data.key] = value
				}
			}
		}

		this.props.SetQuotes(quotes)
	}

	ToggleLeadOverview(data) {
		if (data != undefined) {
			for (let i = 0; i < this.props.quotes.length; i++) {
				if (this.props.quotes[i]._id == data._id) {
					this.setState({
						leadOverviewActive:!this.state.leadOverviewActive,
						selectedLead:i
					})
					break;
				}
			}
		} else {
			this.setState({
				leadOverviewActive:!this.state.leadOverviewActive,
				selectedLead:-1
			})
		}
	}

	GetPage() {
		if (this.state.currentTab == 0) {
			return (
				<TrackerView 
					settings = {this.props.settings.data}
					quotes = {this.props.quotes}
					ToggleLeadOverview = {this.ToggleLeadOverview}
					statusDrops = {this.props.statusDrops}
				/>
			)
		} else {
			return (
				<AnalyticsView 
					settings = {this.props.settings.data}
					quotes = {this.props.quotes}
					//metrics = {this.state.metrics}
				/>
			)
		}
	}

    render() {
        return(
			<>
				<MenuSideBar 
                    tabs = {["Leads","Analytics"]}
                    SetTab = {this.SetTab}
                    MenuStyle = {this.state.collapsed}
					images = {[
						"https://www.vhpportal.com/repo/assets/icons/calendar.png",
                        "https://www.vhpportal.com/repo/assets/icons/data-analytics.png",
                    ]}
					menuButton = "https://www.vhpportal.com/repo/assets/icons/right-arrow.png"
					CollapseFunction = {this.GetMenuCollapsed}
					bottomActions = {[
						{
							text:"Home",
							src:"https://www.vhpportal.com/repo/assets/icons/home2.png",
							type:"ImageButton",
							keyName:"home",
							ClickFunction:GoHome
						},
						{
							text:"Filters",
							src:"https://www.vhpportal.com/repo/assets/icons/filter.png",
							type:"ImageButton",
							keyName:"home",
							ClickFunction:this.props.ToggleBoolean,
							data:"filterActive"
						}
					]}
					moreActions = {[
						{
							src:"https://www.vhpportal.com/repo/assets/icons/logout.png",
							type:"ImageButton",
							keyName:"home",
							ClickFunction:this.props.LogUserOut
						},
						{
							src:"https://www.vhpportal.com/repo/assets/icons/helpme.png",
							type:"ImageButton",
							keyName:"home",
							ClickFunction:this.RequestHelp
						}
					]}
                />
				<AppContainer id = {this.state.collapsed} containerClass = "side-bar-container">
					{this.GetPage()}
				</AppContainer>
				{this.state.leadOverviewActive&&<FloatContainer>
					<TrackerEditor
						CloseFunction = {this.ToggleLeadOverview}
						data = {this.props.quotes[this.state.selectedLead]}
						UpdateFunction = {this.props.UpdateTracker}
						AddBid = {this.AddBid}
						RemoveBid = {this.RemoveBid}
						EditBid = {this.EditBid}
						settings = {this.props.settings.data}
						SetDropNote = {this.props.SetDropNote}
					/>
				</FloatContainer>}
			</>
        );
    }
}