import React, { Component } from 'react';
import { DropOption } from './DropOption';
import { SetProperty } from '../bin/vhp-tools';
import { TextInput } from '../Inputs/TextInput';
import { FilterDropDownListByText } from '../bin/vhp-tools';


/**
 * Input component which takes a search function with an optional key and returns filtered data.
 * @param {Function} ChangeFunction | the function called by the component each time a drop down is triggered.
 * @param {Object} list | the list of options, in the format {text, value}.
 * @param {Boolean} canClear | determines whether the DropDown can be deselected.
 * @param {Object} selected | the initially selected option.
 * @param {Object} fallBackOption | Unknown.
 * @param {String} id | the id of the component.
 * @param {String} dropDownClass | the class associated with the container of the dropdown.
 * @param {*} data | the data passed into the ChangeFunction.
 * @returns the selected {text,value} and the data passed into the component.
 */
export class DropDown extends Component {
    constructor(props) {
        super(props)

        this.state = {
            selected:this.props.selected,
            list:this.props.list,
            listActive:false,
            clearVisible:false,
			search:""
        }

        //Determine the fallback for the clear selected function
        if (this.props.fallBackOption != undefined) {
            this.fallBackOption = JSON.parse(JSON.stringify(this.props.fallBackOption))
        } else {
            this.fallBackOption = JSON.parse(JSON.stringify(this.props.selected))
        }

        this.ToggleDroplist = this.ToggleDroplist.bind(this)
        this.SelectOption = this.SelectOption.bind(this)
        this.GenerateList = this.GenerateList.bind(this)
        this.ClearSelected = this.ClearSelected.bind(this)
		this.SetProperty = SetProperty.bind(this)
    }

    /**
     * On update, check if the list in props has been modified
     * Right now, just checks the length
     */
    componentDidUpdate(prevProps, prevState) {
        //Flip the visibility of the clear button depending on the value of the drop down
        if (this.props.canClear) {
            if (this.state.clearVisible == false && this.state.selected.value != "") {
                this.setState({
                    clearVisible:true
                })
            } else if (this.state.clearVisible == true && this.state.selected.value == "") {
                this.setState({
                    clearVisible:false
                })
            }
        }

		if (prevProps.selected.text != this.props.selected.text && prevState.selected.value == this.state.selected.value) {
			this.setState({
				selected:this.props.selected
			})
		}
    }

    /**
     * Clears the currently selected option, using this.fallBackOption
     */
    ClearSelected() {
        this.setState({
            selected:this.fallBackOption
        },()=>{
            this.ToggleDroplist();
            this.props.ChangeFunction(this.state.selected, this.props.data);
        })
    }

    /**
     * Resets list and toggles drop list visibility
     */
    ToggleDroplist() {
        this.setState({
            listActive:!this.state.listActive
        })
    }

    /**
     * 
     * @param {object} selectedOption : selected object
     */
    SelectOption(selectedOption) {
        //If it's not already selected update it.
        if (this.state.selected.value != selectedOption.value) {
            for (let option in this.props.list) {
                if (this.props.list[option].value == selectedOption.value) {
                    this.setState({
                        selected:selectedOption
                    },()=>{
                        this.props.ChangeFunction(this.state.selected, this.props.data)
                    })
                    this.ToggleDroplist();
                    break
                }
            }
        }
    }

    GenerateList() {
		let list = FilterDropDownListByText(this.props.list, this.state.search, this.props.filterMode)
		let items = list.map((obj) => {
			if (obj.value != this.state.selected.value) {
				return(<DropOption
                        text = {obj.text}
                        value = {obj.value}
                        id = {obj.id}
                        ClickFunction = {this.SelectOption}
                        data = {obj}
                        selected={false}
                        key = {obj.value}
						title = {obj.title}
				    />)
			    }
		    }	
		);

		return items
    }

    render() {
        return(
            <div className='dropdown-cont'>
                 <DropOption 
                    text = {this.state.selected.text}
                    value = {this.state.selected.value}
                    id = {this.state.selected.id}
                    ClickFunction = {this.ToggleDroplist}
                    selected = {true}
                    testid = {this.props.id}
                    optionClass={this.dropDownClass}
                    clearVisible = {this.state.clearVisible}
                    ClearFunction = {this.ClearSelected}
                >
                    
                </DropOption>
                {this.state.listActive&&<div className = "dropdown-list" role = "listbox">
					{this.props.searchable&&<TextInput 
						inputClass = 'search-bar'
						value = {this.state.search}
						ChangeFunction = {this.SetProperty}
						data = 'search'
						placeholder = 'Type to search...'
					/>}
					{this.GenerateList()}
				</div>}
            </div>
        );
    }
}