import React, { Component } from 'react';

import { InputForm, Clone, TextButton } from 'vhp-component-library';

/**
 * @warn | do not call API in this.props.SaveFunction, as function is called on each keystroke within an input
 * @todo | Add support for other dropdown types
 */
export class PopoutForm extends Component {
	constructor(props) {
        super(props)

        this.state = {
			popoutActive:false,
			form:{},
			formReady:false
        }

		this.TogglePopout = this.TogglePopout.bind(this)
		this.SetFormProperty = this.SetFormProperty.bind(this)
		this.GetForm = this.GetForm.bind(this)
    }

	componentDidMount() {
		//Load the form vars into this.state.form
		let form = {}
		for (let index in this.props.formdata) {
			if (this.props.formdata[index].inputType == 'DropDown') {
				if (this.props.formdata[index].selected != undefined) {
					form[this.props.formdata[index].data] = this.props.formdata[index].selected
				} else {
					form[this.props.formdata[index].data] = {text:"Select one",value:""}
				}
			} else {
				if (this.props.formdata[index].value != undefined) {
					form[this.props.formdata[index].data] = this.props.formdata[index].value
				} else {
					form[this.props.formdata[index].data] = ""
				}
			}
		}
		//Load the formdata, setting Click/ChangeFunction to this.SetFormProperty
		//Check for 'PopoutForm' as an inputType - if equal to, skip form item
		this.setState({
			form:form,
			formReady:true
		})
	}

	GetForm() {
		if (this.state.formReady) {
			let formdata = []
			for (let index in this.props.formdata) {
			let newForm = Clone(this.props.formdata[index])
				newForm.value = this.state.form[newForm.data]
				newForm.selected = this.state.form[newForm.data]
				newForm.ChangeFunction = this.SetFormProperty
				newForm.ClickFunction = this.SetFormProperty
				formdata.push(newForm)
			}
			return <InputForm 
				formdata = {formdata}
			/>
		} else {
			<InputForm 
				formdata = {[]}
			/>
		}
	}

	TogglePopout() {
		//Toggle Visibility of popout
		if (this.state.popoutActive == false) {
			this.setState({
				popoutActive:true
			})
		} else {
			this.setState({
				popoutActive:false
			})
		}
	}

	SetFormProperty(value, key) {
		//Set the value matching key in this.state.form
		let form = Clone(this.state.form)
		form[key] = value
		this.setState({
			form:form
		},()=>{
			if (this.props.SaveFunction) {
				this.props.SaveFunction(form, this.props.data)
			}
		})
	}

	render() {
        return(
			<div className='popout-form-container'>
				{this.state.popoutActive&&<div className={'popout-form '+this.props.position}>
					{this.state.formReady>0&&this.GetForm()}
				</div>}
				<TextButton 
					text = {this.props.text}
					buttonClass = {"popout-form-button " + this.props.buttonClass + " " + this.state.popoutActive}
					id = {this.props.id}
					ClickFunction = {this.TogglePopout}
				/>
			</div>
        );
    }
}