import React, { Component } from 'react';
import { AppContainer, Card, InputForm, MenuTabBar, SetTab } from 'vhp-component-library';

import '../Styles/LeadSummary.css'
import { GENmetrics } from '../../bin/metrics';
import { priceformat } from '../../bin/vg-gentools';
import { GetQuotesByMonth } from '../../bin/tracker-tools';

/**
 * 
 */
export class LeadSummary extends Component {
    constructor(props) {
        super(props)

		this.GenerateSummaryForm = this.GenerateSummaryForm.bind(this)
		this.GenerateMonthForm = this.GenerateMonthForm.bind(this)
    }

	GenerateSummaryForm() {
		if (this.props.quotes.length > 1 && this.props.settings != undefined) {
			/*let quotes = []
			let lastYear = new Date(new Date().setFullYear(new Date().getFullYear() - 1));
			lastYear = lastYear.getFullYear()
			for (let i = 0; i < this.props.quotes.length; i++) {
				if (this.props.quotes[i].date.slice(0, 4) != lastYear) {
					if (this.props.quotes[i].date.slice(5,7) == this.props.currentMonth.value) {
						quotes.push(this.props.quotes[i])
					}
				}
			}*/
			let quotes = GetQuotesByMonth(this.props.quotes, this.props.currentMonth.value, true)
			let data = GENmetrics(this.props.quotes,this.props.settings.reporting, 2023)

			return (<InputForm 
				formdata = {[
					{
						title:"Leads",
						value:data.leads,
						formItemClass:"lead-sum-form-item",
						id:"leads"
					},
					{
						title:"Wins",
						value:data.wins,
						formItemClass:"lead-sum-form-item",
						id:"wins"
					},
					{
						title:"Close%",
						value:data.close,
						formItemClass:"lead-sum-form-item",
						id:"closepercent"
					},
					{
						title:"Revenue",
						value:priceformat(data.rev, true),
						formItemClass:"lead-sum-form-item",
						id:"revenue"
					},
					{
						title:"RpO",
						value:priceformat(data.rpo, true),
						formItemClass:"lead-sum-form-item",
						id:"rpo"
					}
				]}
			/>)
		}
	}

	GenerateMonthForm() {
		if (this.props.settings != undefined) {
			let co = false
			if (this.props.currentMonth.value == 0) {co = true}
			let quotes = GetQuotesByMonth(this.props.quotes, this.props.currentMonth.value, true)
			let data = GENmetrics(quotes,this.props.settings.reporting, 2023, co)
			return (<InputForm 
				formdata = {[
					{
						title:"Leads",
						value:data.leads,
						formItemClass:"lead-sum-form-item",
						id:"leads"
					},
					{
						title:"Wins",
						value:data.wins,
						formItemClass:"lead-sum-form-item",
						id:"wins"
					},
					{
						title:"Close%",
						value:data.close,
						formItemClass:"lead-sum-form-item",
						id:"closepercent"
					},
					{
						title:"Revenue",
						value:priceformat(data.rev, true),
						formItemClass:"lead-sum-form-item",
						id:"revenue"
					},
					{
						title:"RpO",
						value:priceformat(data.rpo, true),
						formItemClass:"lead-sum-form-item",
						id:"rpo"
					}
				]}
			/>)
		}
		
	}

    render() {
        return(
            <div className='lead-summary-form'>
				<Card
					titlebar = {true}
					cardClass = "outline-card bg-light-gray lead-info-card"
					title = {this.props.currentMonth.text}
				>
					{this.GenerateMonthForm()}
				</Card>
				<Card
					titlebar = {true}
					cardClass = "outline-card bg-light-gray lead-info-card"
					title = "Total"
				>
					{this.GenerateSummaryForm()}
				</Card>
			</div>
        );
    }
}