import './App.css';

import {AppBox} from './Components/VHP/Admin/AppBoxRestruct';
import { ResidentialTracker } from './ResTracker/ResidentialTracker';

import './Filters.css'
import './ViewToggles.css'

import './MenuSideBar.css'
import './DropNote.css'
import './ExitDialogue.css'

/**
 * All you give the AppBox is a basic set of settings - everything else (quick actions, etc) should only
 * be customized by your application itself. The AppBox works just as a basic container with a UserForm & login included
 */
var configdefault = {
	tb:{
		active:false
	},
	user:{
		active:true
	},
	dock:{
		active:false
	},
	settings:{
		dev:false
	}
}

function GetApp() {
	if (window.ipcRenderer != undefined) {
		return <ResidentialTracker 
			settings = {configdefault.settings}
			user = {{user:"ipc invoke call goes here"}}
		/>
	} else {
		return <AppBox config={configdefault}/>
	}
}

function App() {
  	return (
		<div className='App'>
			<AppBox config={configdefault}/>
		</div>
		
  	);
}

export default App;
