import { GENmetrics, GENsourcetables } from "./metrics";

let months = [
	"CO",
	"JAN",
	"FEB",
	"MAR",
	"APR",
	"MAY",
	"JUN",
	"JUL",
	"AUG",
	"SEP",
	"OCT",
	"NOV",
	"DEC"
];

export function GetSoldBidTotal(bids) {
	let count = 0;
	for (let i = 0; i < bids.length; i++) {
		if (bids[i].sold == true) {
			count = count + Number(bids[i].amount)
		}
	}

	return count
}

export function GetUnsoldBidTotal(bids) {
	let count = 0;
	for (let i = 0; i < bids.length; i++) {
		if (bids[i].sold == false) {
			count += Number(bids[i].amount)
		}
	}

	return count
}

export function GetDataSets(data) {
	let retval = []
	let sum = 0;
	for (let i = 0; i < data.length; i++) {
		sum = sum + Number(data[i].rev)
		retval.push({
			Revenue:Number(data[i].rev),
			"Sum Total":sum,
			Month:months[i]
		})
	}

	return retval
}

export function GetBidChartData(bids) {
	let data = {}
	for (let i = 0; i < bids.length; i++) {
		if (bids[i].sold == true) {
			if (data[bids[i].cat] == undefined) {
				data[bids[i].cat] = {
					amount:Number(bids[i].amount),
					count:1
				}
			} else {
				data[bids[i].cat].amount += Number(bids[i].amount)
				data[bids[i].cat].count += 1
			}
		}
	}
	return data
}

export function GetBidsSet(list) {
	//Loop through all quotes and create a summary of sold bids
	let bids = []
	for (let i = 0; i < list.length; i++) {
		for (let j = 0; j < list[i].bids.length; j++) {
			if (list[i].bids[j].sold == true && Number(list[i].bids[j].amount) > 0) {
				bids.push(list[i].bids[j])
			}
		}
	}
	let data = GetBidChartData(bids)
	let retval = []
	for (let key in data) {
		retval.push({
			count:data[key].count,
			cat:key
		})
	}

	return retval
}

export function GetTopCategory(list) {
	let bids = []
	for (let i = 0; i < list.length; i++) {
		for (let j = 0; j < list[i].bids.length; j++) {
			if (list[i].bids[j].sold == true && Number(list[i].bids[j].amount) > 0) {
				bids.push(list[i].bids[j])
			}
		}
	}
	let data = GetBidChartData(bids)
	let top = null
	for (let key in data) {
		if (top == null) {
			top = key
		} else {
			if (data[top].count <= data[key].count) {
				top = key
			}
		}
	}

	if (top == "") {
		return "Not given"
	}
	return top
}

