export function GetMonthlyData(list, settings) {
	let tableData = [
		"CO",
		"01",
		"02",
		"03",
		"04",
		"05",
		"06",
		"07",
		"08",
		"09",
		"10",
		"11",
		"12"
	];

	let data = []
	let lastYear = new Date(new Date().setFullYear(new Date().getFullYear() - 1));
	lastYear = lastYear.getFullYear()
	lastYear = 2022 //@todo remove
	let thisYear = new Date(new Date().setFullYear(new Date().getFullYear() - 0));
	thisYear = thisYear.getFullYear()
	thisYear = 2023 //@todo remove
	for (let i = 0; i < tableData.length; i++) {
		let quotes = []
		for (let j = 0; j < list.length; j++) {
			if (list[j].date != null) {
				if (tableData[i] == "CO") {
					//Return carryover		
					if (list[j].date.slice(0, 4) != thisYear) {
						quotes.push(list[j])
					}
				} else {
					if (list[j].date.slice(0, 4) == thisYear) {
						if (list[j].date.slice(5,7) == tableData[i]) {
							quotes.push(list[j])
						}
					}
				}
			}
		}
		//@todo remove
		//if(i<=1+new Date().getMonth()){ //dont request future months
			let metrics = GENmetrics(quotes,settings, 2023)
			metrics.month = months[i]
			data.push(metrics)
		//}
	}
	console.log(data)
	return data
}

export function GetSourcePerformance(list, settings) {
	let data = GENsourcetables(list, settings)
	let top = ""
	let began = false;
	for (let key in data.source) {
		if (!began) {
			began = true
			top = key
		}
		if (data.source[key].wins >= data.source[top].wins) {
			top = key
		}
	}

	return {
		best:data.source[top].source,
		worst:""
	}
}

export function GetLeadPerformance(list, settings) {
	let data = GENsourcetables(list, settings)
	let top = ""
	let began = false;
	for (let key in data.lead) {
		if (!began) {
			began = true
			top = key
		}
		if (data.lead[key].wins >= data.lead[top].wins) {
			top = key
		}
	}

	return {
		best:data.lead[top].lead,
		worst:""
	}
}

export function GetQuotesByMonth(quotes, currentMonth, includeCO = false) {
	let retval = []
	let lastYear = new Date(new Date().setFullYear(new Date().getFullYear() - 1));
	lastYear = lastYear.getFullYear()
	lastYear = 2022 //@todo remove
	let thisYear = new Date(new Date().setFullYear(new Date().getFullYear() - 0));
	thisYear = thisYear.getFullYear()
	thisYear = 2023 //@todo remove
	let co = false;
	for (let i = 0; i < quotes.length; i++) {
		if (quotes[i].date != null) {
			if (currentMonth == 0 && includeCO == true) {
				co = true
				if (quotes[i].date.slice(0, 4) != thisYear) {
					retval.push(quotes[i])
				}
			} else {
				if (quotes[i].date.slice(0, 4) != lastYear) {
					if (quotes[i].date.slice(5,7) == currentMonth) {
						retval.push(quotes[i])
					}
				}
			}
		} else {
			if (quotes[i].prstdate != null) {
				if (currentMonth == 0 && includeCO == true) {
					co = true
					if (quotes[i].prstdate.slice(0, 4) != thisYear) {
						retval.push(quotes[i])
					}
				} else {
					if (quotes[i].prstdate.slice(0, 4) != lastYear) {
						if (quotes[i].prstdate.slice(5,7) == currentMonth) {
							retval.push(quotes[i])
						}
					}
				}
			}
		}
	}

	return retval
}

export function GetTopBidCategory(bids) {
	let data = GetBidChartData(bids)
	let top = null
	for (let key in data) {
		if (top == null) {
			top = key
		} else {
			if (data[top].count <= data[key].count) {
				top = key
			}
		}
	}

	return top
}

export function GetTopBidAmount(bids) {
	let data = GetBidChartData(bids)
	let top = null
	for (let key in data) {
		if (top == null) {
			top = key
		} else {
			if (data[top].amount <= data[key].amount) {
				top = key
			}
		}
	}

	return data[top].amount
}

export function GetBidData(bids) {
	///Combine by category
	let data = GetBidChartData(bids)
	let retval = []
	for (let key in data) {
		retval.push({
			amount:data[key].amount,
			cat:key
		})
	}

	return retval
}

export var abid = (data) => {
	return {
		amount:data.amount,
		bookprice:true,
		cat:data.category.value,
		comp:data.comp.value,
		finance:"",
		progress:data.progress.value,
		saletype:data.saletype.value,
		sold:data.sold,
		customerTotal:data.customerTotal,
		ameren:data.ameren,
		manfRebate:data.manfRebate,
		specialRebate:data.specialRebate
	}
}

export function CalculateBidAmount(data) {
	return (+data.customerTotal) + (+data.ameren) + ((+data.manfRebate) / 2 ) + (+data.specialRebate)
}