import React, { Component } from 'react';
import { BasicHeader, Card, CardContent, DataTable, Table, FormItem, InputForm, MenuTabBar, TextButton, SetTab, TableRow, Clone, SortTable } from 'vhp-component-library';

import '../Styles/AnalyticsView.css';
import { CREATEplaces, GENsourcetables } from '../../bin/metrics';
import { GENmetrics } from '../../bin/metrics';
import { Area, CartesianGrid, ComposedChart, Legend, Pie, PieChart, ResponsiveContainer, Tooltip, XAxis, YAxis, Bar, Cell } from 'recharts';
import { SORTlist, priceformat, formatter } from '../../bin/vg-gentools';
import { GetDataSets, GetBidChartData, GetBidsSet, GetTopCategory, GetBottomCategory, GetHighestPayingCategory, GetMonthlyData, GetSourcePerformance, GetLeadPerformance } from '../../bin/tracker-tools';

const COLORS = [
	'#0088FE', 
	'#00C49F', 
	'#FFBB28', 
	'#FF8042',

	'#B99095', 
	'#FCB5AC', 
	'#7db2a0', 
	'#3D5B59',
	'#347DC6',
	'#009a68'
];

export class AnalyticsView extends Component {
    constructor(props) {
        super(props)

		this.state = {
			currentTab:0,
			filters:[
				{},
				{filter:"option", order:1},
				{filter:"zip", order:1},
				{filter:"source", order:1},
				{filter:"lead", order:1}
			]
		}

		this.GetPage = this.GetPage.bind(this)
		this.GetSummaryForm = this.GetSummaryForm.bind(this)
		this.GetSummaryChart = this.GetSummaryChart.bind(this)
		this.ToggleSearch = this.ToggleSearch.bind(this)

		this.SetTab = SetTab.bind(this)
    }

	GetSummaryForm() {
		let data = GENmetrics(this.props.quotes,this.props.settings.reporting, 2023)
		let forms = []

		let tableData = []

		for (let key in data.opts) {
			tableData.push({
				option:this.props.settings.reporting.cat[key].descr,
				total:data.opts[key].total,
				leads:data.opts[key].leads,
				wins:data.opts[key].wins,
				winper:data.opts[key].winper,
				rpo:data.opts[key].rpo
			})
		}

		tableData = SORTlist(tableData, this.state.filters[1].filter, this.state.filters[1].order)

		forms.push(
			<div id = 'options-table'>
				<Table 
					data = {tableData}
					headers = {true}
					SortFunction = {SortTable}
					fields = {[
						{option:"Option"},
						{total:"Total"},
						{leads:"Leads"},
						{wins:"Wins"},
						{winper:"Win %"},
						{rpo:"RpO"}
					]}
					FormatFunctions = {[
						null,
						priceformat,
						null,
						null,
						null,
						priceformat
					]}
				/>
			</div>
		)

		return forms
	}

	GetSummaryChart() {
		let quotes = []
		let lastYear = new Date(new Date().setFullYear(new Date().getFullYear() - 1));
		lastYear = lastYear.getFullYear()
		lastYear = 2022 //@todo remove
		for (let i = 0; i < this.props.quotes.length; i++) {
			if (this.props.quotes[i].date != null) {
				if (this.props.quotes[i].date.slice(0, 4) != lastYear) {
					quotes.push(this.props.quotes[i])
				}
			}
		}
		let data = GENmetrics(this.props.quotes,this.props.settings.reporting, 2023)
		
		return (
			<div className='summary-chart-cont'>
				<div className='sum-charts-cont'>
					<div className='data-chart'>
						<BasicHeader 
							text = "Revenue by Month"
							headerClass = "left-align month-header"
						/>
						<ResponsiveContainer height="90%" width="95%">
							<ComposedChart
								data = {GetDataSets(GetMonthlyData(this.props.quotes,this.props.settings.reporting))}
								margin={{
									top:10,
									right:10,
									left:50,
									bottom:0
								}}
							>
								<CartesianGrid strokeDasharray="3 3"/>
								<XAxis dataKey='Month' padding={{bottom:0}}/>
								<YAxis tickFormatter={priceformat}/>
								<Tooltip/>
								<Legend />
								<Area dataKey="Sum Total" type = "monotone" stroke="#1260c1" fill="#1260c1" />
								<Bar type="monotone" dataKey = "Revenue" fill="#335856" barSize={15}/>
							</ComposedChart>
						</ResponsiveContainer>
					</div>
					<div className='summary-form-cont'>
						<InputForm 
							formdata = {[
								{
									title:"Number of Sales Calls",
									value:data.leads,
									id:"numsalescalls"
								},
								{
									title:"Sales Calls Won",
									value:data.wins,
									id:"salescallswon"
								},
								{
									title:"Closing Percentage",
									value:data.close,
									id:"closingperc"
								},
								{
									title:"Revenue",
									value:priceformat(data.rev),
									id:"rev"
								},
								{
									title:"Revenue per Opportunity",
									value:priceformat(data.rpo),
									id:"revperop"
								},
								{
									title:"Total Leads",
									value:data.gleads,
									id:"totalleads"
								},
								{
									title:"Cancelled Leads",
									value:(data.gleads-data.leads),
									id:"cancelledleads"
								}
							]}
						/>
					</div>
				</div>
				<div className='pie-chart'>
					<ResponsiveContainer width="100%" height="100%">
						<PieChart>
							<Pie
								data = {GetBidsSet(this.props.quotes)}
								dataKey="count"
								nameKey="cat"
								cx="60%"
								cy="50%"
								outerRadius={60}
								innerRadius={30}
								fill='#8884d8'
								label={formatter}
							>
								{GetBidsSet(this.props.quotes).map((entry, index) => (
									<Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
								))}
							</Pie>
							<Legend 
								align = "left"
								layout='horiztonal'
							/>
							<Tooltip />
						</PieChart>
					</ResponsiveContainer>
					<div className='pie-chart-form'>
						<div className='form-top'>
							<FormItem
								data = {{
									title:"Most Frequently Sold",
									value:GetTopCategory(this.props.quotes),
									formItemClass:"capitalize"
								}}
							/>
						</div>
						<div className='form-flex'>
							<FormItem
								data = {{
									title:"Least Frequently Sold",
									value:GetBottomCategory(this.props.quotes),
									formItemClass:"capitalize"
								}}
							/>
							<FormItem
								data = {{
									title:"Highest Paying Category (Sum Total)",
									value:GetHighestPayingCategory(this.props.quotes),
									formItemClass:"capitalize"
								}}
							/>
							<FormItem
								data = {{
									title:"Highest Performing Source",
									value:GetSourcePerformance(this.props.quotes,this.props.settings.reporting).best,
									formItemClass:"capitalize"
								}}
							/>
						</div>
					</div>
				</div>
			</div>
		)
	}

	ToggleSearch(data) {
		let filters = Clone(this.state.filters)
		filters[data.index].filter = data.key
		if (filters[data.index].order == -1) {
			filters[data.index].order = 1
		} else {
			filters[data.index].order = -1
		}

		this.setState({
			filters:filters
		})
	}

	GetPage() {
		if (this.state.currentTab == 0) {
			//console.log(this.props.metrics, "METRICS")
			return this.GetSummaryChart()
		} else if (this.state.currentTab == 1) {
			return <div className='summary-page-container'>
				{this.GetSummaryForm()}
			</div>
		} else if (this.state.currentTab == 2) {
			let zipData = CREATEplaces(this.props.quotes)
			zipData = SORTlist(zipData, this.state.filters[2].filter, this.state.filters[2].order)
			return (
				<Table 
					data = {zipData}
					SortFunction = {SortTable}
					headers = {true}
					fields = {[
						{zip:"ZIP Code"},
						{leads:"Leads"},
						{wins:"Wins"},
						{winper:"Win %"},
						{total:"Total"},
						{rpo:"RpO"}
					]}
					FormatFunctions = {[
						null,
						null,
						null,
						null,
						priceformat,
						priceformat
					]}
				/>
			)
		} else if (this.state.currentTab == 3) {
			let sourceData = GENsourcetables(this.props.quotes, this.props.settings.reporting)
			sourceData.source = SORTlist(sourceData.source, this.state.filters[3].filter, this.state.filters[3].order)

			return (
				<Table 
					data = {sourceData.source}
					headers = {true}
					SortFunction = {SortTable}
					fields = {[
						{source:"Source"},
						{leads:"Leads"},
						{wins:"Wins"},
						{winper:"Win %"},
						{total:"Total"},
						{rpo:"RpO"}
					]}
					FormatFunctions = {[
						null,
						null,
						null,
						null,
						priceformat,
						priceformat
					]}
				/>
			)
		} else if (this.state.currentTab == 4) {
			let sourceData = GENsourcetables(this.props.quotes, this.props.settings.reporting)
			sourceData.lead = SORTlist(sourceData.lead, this.state.filters[3].filter, this.state.filters[3].order)

			return (
				<Table 
					data = {sourceData.lead}
					headers = {true}
					SortFunction = {SortTable}
					fields = {[
						{lead:"Lead"},
						{leads:"Leads"},
						{wins:"Wins"},
						{winper:"Win %"},
						{total:"Total"},
						{rpo:"RpO"}
					]}
					FormatFunctions = {[
						null,
						null,
						null,
						null,
						priceformat,
						priceformat
					]}
				/>
			)
			
		}
	}

    render() {
        return(
            <Card
				titlebar = {true}
				cardClass = "outline-card no-padding"
				id = "analytics-card"
				title = "Analytics"
			>
				<MenuTabBar 
					tabs = {["Summary", "Categories", "Places", "Sources", "Leads"]}
					SetTab = {this.SetTab}
				/>
				{this.GetPage()}
			</Card>
        );
    }
}