import React, { Component } from 'react';
import { MenuTabBar, Card, Months, TextButton, BasicHeader, CardContent, SortTable, GetDropDownFromValue, GetDropDownTextFromValue, Table } from 'vhp-component-library';

let tableData = [
	"CO",
	"01",
	"02",
	"03",
	"04",
	"05",
	"06",
	"07",
	"08",
	"09",
	"10",
	"11",
	"12"
]

import '../Styles/TrackerMonthView.css'
import { SORTlist, SORTtracker, dateformat, toTitleCase } from '../../bin/vg-gentools';

function FilterByMonth(data) {
	let newData = []
	let lastYear = new Date(new Date().setFullYear(new Date().getFullYear() - 1));
	lastYear = lastYear.getFullYear()
	lastYear = 2022 //@todo remove
	let thisYear = new Date(new Date().setFullYear(new Date().getFullYear() - 0));
	thisYear = thisYear.getFullYear()
	thisYear = 2023 //@todo remove
	for (let i = 0; i < data.length; i++) {
		if (data[i].date != null) {
			if (this.state.currentTab == 0) {
				//Return carryover		
				if (data[i].date.slice(0, 4) != thisYear) {
					newData.push(data[i])
				}
			} else {
				if (data[i].date.slice(0, 4) == thisYear) {
					if (data[i].date.slice(5,7) == tableData[this.state.currentTab]) {
						newData.push(data[i])
					}
				}
			}
		} else {
			if (data[i].prstdate != null) {
				if (this.state.currentTab == 0) {
					//Return carryover		
					if (data[i].prstdate.slice(0, 4) != thisYear) {
						newData.push(data[i])
					}
				} else {
					if (data[i].prstdate.slice(0, 4) == thisYear) {
						if (data[i].prstdate.slice(5,7) == tableData[this.state.currentTab]) {
							newData.push(data[i])
						}
					}
				}
			}
		}
	}

	return newData
}

/**
 * @todo Logic for generating the month and 
 */
export class TrackerMonthView extends Component {
    constructor(props) {
        super(props)

        this.state = {
            currentTab:0,
			filter:"projectname",
			order:1
        }
        
        this.SetTab = this.SetTab.bind(this)
		this.ToggleSearch = this.ToggleSearch.bind(this)
		this.FilterByMonth = FilterByMonth.bind(this)
    }

	componentDidMount() {
		this.SetTab(new Date().getMonth() + 1)
	}

	SetTab(TabID) {
		this.setState({
			currentTab:TabID
		},()=>{
			if (TabID == 0) {
				this.props.SetMonth({text:"CO",value:0}, 'currentMonth')
			} else {
				this.props.SetMonth({text:Months[TabID-1].text,value:TabID}, 'currentMonth')
			}
		})
	}

	ToggleSearch(filter) {
		let order = 1
		if (this.state.order == -1) {
			order = 1
		} else {
			order = -1
		}

		this.setState({
			order:order,
			filter:filter
		})
	}

    render() {
        return(
            <>
				<Card 
					cardClass = "no-padding outline-card"
					id = "tracker-month-view"
					title = "Lead Tracker"
					titlebar = {true}
				>
					<MenuTabBar 
						tabs = {["CO", "Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"]}
						SetTab = {this.SetTab}
						MenuStyle = "top-bar"
						default = {new Date().getMonth() + 1}
					/>
					<Table 
						data = {this.props.quotes}
						fields = {[
							{"projectname":"Project Name"},
							{"estimator":"Consultant"},
							{"date":"Date"},
							{"stage":"Stage"},
							{"status":"Status"},
							{"client":"Client"}
						]}
						headers = {true}
						FilterFunction = {this.FilterByMonth}
						SortFunction = {SORTtracker}
						FormatFunctions = {[null, null, dateformat, toTitleCase, GetDropDownTextFromValue, null]}
						formatData = {[null, null, null, null, this.props.statusDrops]}
						ClickFunction = {this.props.ToggleLeadOverview}
					/>
				</Card>
            </>
        );
    }
}