import React, { Component } from 'react';
import { Card, InputForm, SetProperty, TextArea, TextButton, Warning, PaymentMethodList } from 'vhp-component-library';

function GetMessage(count) {
	if (count == 1) {
		return "1 repair has been marked as 'RETURN FOR'"
	} else {
		return count + " repairs have been marked as 'RETURN FOR'"
	}
}

/**
 * A card used for marking a ticket as complete.
 * @param {Object} data | the data passed into and out of the card
 * @param {Object} ticket | the ticket currently selected
 * @param {Function} CompleteTicket | the function called when completing and finalizing the ticket
 * @param {String} closeData | the property associated with toggling the card's visibility
 * @param {Function} CloseFunction | the function used to toggle visibility of the card
 */
export class TicketCompleteCard extends Component {
	constructor(props) {
		super(props)

		this.state = {
			emailed:false,
			paymentMethod:{text:"Select Payment Method", value:null},
			addlDetails:'',
			returnNotes:'',
			returnVisit:false,
			reqs:{
				paymentMethod:'',
				returnNotes:''
			},
			returnCount:0
		}

		this.SetProperty = SetProperty.bind(this)
		this.CheckFormFill = this.CheckFormFill.bind(this)
	}

	componentDidMount() {
		let returnCount = 0;
		let returnVisit = false;
		for (let i = 0; i < this.props.ticket.repairs.length; i++) {
			for (let repair in this.props.ticket.repairs[i]){
				if (this.props.ticket.repairs[i][repair].appr == "RETURN FOR") {
					returnCount+=1
					returnVisit = true
				}
			}
		}
		this.setState({
			returnCount:returnCount,
			returnVisit:returnVisit
		})
	}

	CheckFormFill() {
		if (this.state.paymentMethod.value == null || (this.state.returnVisit == true && this.state.returnNotes == "")) {
			let reqs = {
				paymentMethod:'',
				returnNotes:''
			}
			if (this.state.paymentMethod.value == null) {
				reqs.paymentMethod = 'form-item-empty'
			}
			if(this.state.returnVisit == true && this.state.returnNotes == "") {
				reqs.returnNotes = 'form-item-empty'
			}
			this.setState({
				reqs:reqs
			})
		} else {
			this.props.CompleteTicket({
				emailed:this.state.emailed,
				paymentMethod:this.state.paymentMethod.value,
				returnVisit:this.state.returnVisit,
				returnNotes:this.state.returnNotes,
				addlDetails:this.state.addlDetails,
				...this.props.data
			})
		}
	}

	render() {
		return(
			<Card
				id = "ticket-close-card"
				titlebar = {true}
				title = "Complete & Finalize Ticket"
				actions = {{minimize:null, close:this.props.CloseFunction}}
				data = {{minimize:null, close:this.props.closeData}}
			>
				<div className='email-text'>Confirm that the following items have been completed before proceeding:</div>
				<div className='form'>
					<InputForm
						formdata = {[
							{
								title:"Invoice Emailed:",
								inputType:"Checkbox",
								data:"emailed",
								value:this.state.emailed,
								ClickFunction:this.SetProperty
							},
							{
								title:"Payment Method:",
								inputType:"DropDown",
								data:"paymentMethod",
								selected:this.state.paymentMethod,
								list:PaymentMethodList,
								ChangeFunction:this.SetProperty,
								formItemClass:this.state.reqs.paymentMethod
							},
							{
								title:"Payment Details:",
								inputType:"TextInput",
								data:"addlDetails",
								value:this.state.addlDetails,
								ChangeFunction:this.SetProperty,
								placeholder:"CC#, Check #, etc."
							}
						]}
					/>
					{this.state.returnVisit&&
						<>
							<Warning message = {GetMessage(this.state.returnCount)}/>
							<div className={'form-info-item ' + this.state.reqs.returnNotes}>
								<div className='form-info-title'>Notes for Return Visit:</div>
								<TextArea 
									ChangeFunction = {this.SetProperty}
									data = "returnNotes"
									value = {this.state.returnNotes}
								/>
							</div>
						</>
					}
				</div>
				<div className='action-row right'>
					<TextButton 
						text = "Complete and Finalize Ticket"
						ClickFunction = {this.CheckFormFill}
						id = "close-ticket-button"
					/>
				</div>
			</Card>
		)
	}
}