import React, { Component } from 'react';
import { Card, TextButton, TextInput} from 'vhp-component-library';

/**
 * A Card for sending emails. Triggers an email function
 * @param {String} email | the email address used
 * @param {String} closeData | the property associated with toggling the card's visibility
 * @param {String} message | optional message in the card
 * @param {Function} EmailFunction | the function for emailing, with param *email*
 * @param {Function} CloseFunction | the function used to toggle visibility of the card
 */
export class EmailFormCard extends Component {
	constructor(props) {
		super(props)

		this.ToggleEmail = this.ToggleEmail.bind(this)
	}

	ToggleEmail() {
		this.props.EmailFunction(this.props.email)
		this.props.CloseFunction(this.props.closeData)
	}

	render() {
		return(
			<Card
				id = "emailer-card"
				titlebar = {true}
				title = "Emailer"
				actions = {{minimize:null, close:this.props.CloseFunction}}
				data = {{minimize:null, close:this.props.closeData}}
			>
				<div className='email-text'>{this.props.message||'The following email address will be used:'}</div>
				<TextInput 
					value = {this.props.email}
					ChangeFunction = {this.props.SetProperty}
					data = "email"
				/>
				<div className='action-row right'>
					<TextButton 
						text = "Send Email"
						ClickFunction = {this.ToggleEmail}
						data = {this.props.email}
					/>
				</div>
			</Card>
		)
	}
}