import React, { Component } from 'react';
import { CardContent, Card, TextInput, FormItem, DataTable, SetProperty, Table, SortTable, SearchArray, GetDropDownTextFromValue } from 'vhp-component-library';

import '../Styles/TrackerSearchCard.css'

import { SORTtracker, dateformat, toTitleCase } from '../../bin/vg-gentools';

export class TrackerSearchCard extends Component {
    constructor(props) {
        super(props)

        this.state = {
            status:true,
			stage:true,
			projectname:true,
			client:true,
			search:this.props.search||""
        }

		this.SetProperty = SetProperty.bind(this)
    }

    render() {
        return(
            <Card
				id = "tracker-search-card"
				titlebar = {true}
				title = "Search"
				actions = {{minimize:null, close:this.props.CloseFunction}}
				data = {{minimize:null, close:this.props.closeData}}
			>
				<div className='action-row center' id = "tracker-search-actions">
					<FormItem
						data = {{
							title:"Search:",
							inputType:"TextInput",
							value:this.state.search,
							ChangeFunction:this.SetProperty,
							data:"search"
						}}
					/>
					<FormItem
						data = {{
							title:"Status:",
							inputType:"Checkbox",
							value:this.state.status,
							ClickFunction:this.SetProperty,
							data:"status"
						}}
					/>
					<FormItem
						data = {{
							title:"Stage:",
							inputType:"Checkbox",
							value:this.state.stage,
							ClickFunction:this.SetProperty,
							data:"stage"
						}}
					/>
					<FormItem
						data = {{
							title:"Project Name:",
							inputType:"Checkbox",
							value:this.state.projectname,
							ClickFunction:this.SetProperty,
							data:"projectname"
						}}
					/>
					<FormItem
						data = {{
							title:"Client:",
							inputType:"Checkbox",
							value:this.state.client,
							ClickFunction:this.SetProperty,
							data:"client"
						}}
					/>
				</div>
				<CardContent id = "tracker-search-card-table">
					<Table 
						data = {this.props.quotes}
						ClickFunction = {this.props.ToggleLeadOverview}
						fields = {[
							{projectname:"Project Name"},
							{estimator:"Estimator"},
							{date:"Date"},
							{stage:"Stage"},
							{status:"Status"},
							{client:"Client"}
						]}
						FilterFunction = {SearchArray}
						filterData = {{
							search:this.state.search,
							fields:{
								status:this.state.status,
								stage:this.state.stage,
								projectname:this.state.projectname,
								client:this.state.client
							}
						}}
						FormatFunctions = {[null, null, dateformat, toTitleCase, GetDropDownTextFromValue, null]}
						formatData = {[null, null, null, null, this.props.statusDrops]}
						headers = {true}
						SortFunction = {SORTtracker}
					/>
				</CardContent>
			</Card>
        );
    }
}