import React, { Component } from 'react';
import { CardContent, BasicHeader, TextInput, Image, SetProperty, TextButton, FloatContainer, ToggleBoolean } from 'vhp-component-library';
import { TrackerMonthView } from './TrackerMonthView';
import { LeadSummary } from './LeadSummary';

import '../Styles/TrackerView.css'
import { TrackerSearchCard } from './TrackerSearchCard';

/**
 * Starter class for an application. Contains a set of containers with a MenuBar allowing you to swap between tabs.
 * MenuBar style can be customized, and containers can be added to the Container to add pages to your app
 * Clone and modify this file whenever you want to create a new app that can sit inside App.js or the dock.
 */
export class TrackerView extends Component {
    constructor(props) {
        super(props)

        this.state = {
            currentMonth:{text:"January", value:0},
			searchBoxActive:false,
			search:""
        }

		this.ToggleBoolean = ToggleBoolean.bind(this)
		this.SetProperty = SetProperty.bind(this)
    }

    render() {
        return(
			<>
				<div className='action-row left'id = "titlebar-actions">
					<BasicHeader 
						text = "Leads"
						headerClass = "left-align"
					/>
					<div className='search-bar'>
						<Image src = "https://www.vhpportal.com/repo/assets/icons/search.png"/>
						<TextInput 
							value = {this.state.search}
							ChangeFunction = {this.SetProperty}
							data = "search"
							placeholder = "Type to search (Project Name, Stage, Status, or Client)"
							SubmitFunction = {this.ToggleBoolean}
							submitData = "searchBoxActive"
						/>
					</div>
				</div>
				<CardContent cardContentClass = "no-padding" id = "tracker-view-cont">
					<TrackerMonthView 
						quotes = {this.props.quotes}
						ToggleLeadOverview = {this.props.ToggleLeadOverview}
						SetMonth = {this.SetProperty}
						statusDrops = {this.props.statusDrops}
					/>
					<LeadSummary 
						settings = {this.props.settings}
						quotes = {this.props.quotes}
						currentMonth = {this.state.currentMonth}
					/>
				</CardContent>
				{this.state.searchBoxActive&&<FloatContainer>
					<TrackerSearchCard 
						quotes = {this.props.quotes}
						CloseFunction = {this.ToggleBoolean}
						closeData = "searchBoxActive"
						ToggleLeadOverview = {this.props.ToggleLeadOverview}
						search = {this.state.search}
						statusDrops = {this.props.statusDrops}
					/>
				</FloatContainer>}
			</>
        );
    }
}