import React, { Component } from 'react';
import { Core } from 'vhp-api';
import { CloseNote, ToggleLoadingScreen, BasicLoadingScreen, SetDropNote, Clone, DropNote, DataFilterCard, ToggleBoolean, FloatContainer, GenerateDropDownList, sortarray, SetGlobalFilters, ResetGlobalFilters, FilterDataShallow } from 'vhp-component-library'
import { ResidentialTracker } from './ResidentialTracker';

/**
 * Starter class for an application. Contains a set of containers with a MenuBar allowing you to swap between tabs.
 * MenuBar style can be customized, and containers can be added to the Container to add pages to your app
 * Clone and modify this file whenever you want to create a new app that can sit inside App.js or the dock.
 */
export class AppController extends Component {
    constructor(props) {
        super(props)

        this.state = {
            Mongo:undefined,
			DropNoteProps:{
				showNote: false,
                text: 'Note?',
                level: 'orange',
                timeout: 0
			},
			loading:false,
			loadingMsg:"Loading",
			quotes:[],
			settings:{},
			filters:{
				estimator:"",
				date:"",
				amount:"",
				status:"",
				stage:"",
				bids:false
			},
			filterActive:false,
			statusDrops:[],
			userList:[]
        }

		this.GetSettings = this.GetSettings.bind(this)
		this.GetTrackers = this.GetTrackers.bind(this)
		this.GetUserList = this.GetUserList.bind(this)
		this.UpdateTracker = this.UpdateTracker.bind(this)
		this.SetQuotes = this.SetQuotes.bind(this)
		this.RequestHelp = this.RequestHelp.bind(this)
		this.SetFilters = SetGlobalFilters.bind(this)
		this.ResetFilters = ResetGlobalFilters.bind(this)
		this.GetFilterCardData = this.GetFilterCardData.bind(this)
		
		//REACT
		this.GetApp = this.GetApp.bind(this)
		this.CloseNote = CloseNote.bind(this)
		this.SetDropNote = SetDropNote.bind(this)
		this.ToggleLoadingScreen = ToggleLoadingScreen.bind(this)
		this.ToggleBoolean = ToggleBoolean.bind(this)
    }
    
	componentDidMount() {
		if (this.props.dev == true) {
			//Get the dev Mongo
			this.setState({
				Mongo: new Core({host:"http://3.135.187.89/", sync:false, auth:{user:"VOGCH",pswrd:"vogel123", dev:{comments:false}}})
			},()=>{
				this.GetSettings()
				this.GetTrackers()
				this.GetUserList()
			})
		} else {
			this.setState({
				Mongo: new Core({sync:false, auth:{user:"VOGCH",pswrd:"vogel123", dev:{comments:false}}})
			},()=>{
				this.GetSettings()
				this.GetTrackers()
				this.GetUserList()
			})
		}
	}

	/**
	 * Retrieves settings from electron or the API
	 * @todo Add electron route for settings
	 */
	GetSettings() {
		this.state.Mongo.SENDrequest({
			pack:{
				db:'Replacement',
				collect:'Setting',
				method:"QUERY",
				options:{
					query:{}
				}
			},
			route:'STORE',
			request:'MART'
		}).then(answer=>{
			let statusDrops = []
			statusDrops = GenerateDropDownList(answer.result[0].data.status)
			this.setState({
				settings:answer.result[0],
				statusDrops:statusDrops
			})
		})
	}

	GetTrackers() {
		this.SetDropNote({
			showNote:true,
			level:"blue",
			text:"Loading data",
			timeout:1000
		})
		if (this.props.user.name == "RESMAN") {
			this.state.Mongo.SENDrequest({
				pack:{
					db:'Replacement',
					collect:'Tracking350',
					method:"QUERY",
					options:{
						query:{}
					}
				},
				route:'STORE',
				request:'MART'
			}).then(answer=>{
				if (answer.success) {
					let list = sortarray(answer.result)
					this.setState({
						quotes:list
					})
					this.SetDropNote({
						showNote:true,
						level:"green",
						text:"Data loaded",
						timeout:2000
					})
				} else {
					this.SetDropNote({
						showNote:true,
						level:"red",
						text:answer.msg,
						timeout:0
					})
				}
			})
		} else {
			this.state.Mongo.SENDrequest({
				pack:{
					db:'Replacement',
					collect:'Tracking350',
					method:"QUERY",
					options:{
						query:{estimator:this.props.user.name}
					}
				},
				route:'STORE',
				request:'MART'
			}).then(answer=>{
				if (answer.success) {
					this.setState({
						quotes:sortarray(answer.result, -1, 'date')
					})
					this.SetDropNote({
						showNote:true,
						level:"green",
						text:"Data loaded",
						timeout:10000
					})
				} else {
					this.SetDropNote({
						showNote:true,
						level:"red",
						text:answer.msg,
						timeout:0
					})
				}
			})
		}
	}

	GetUserList() {
		if (this.props.user.name == 'RESMAN') {
			this.state.Mongo.SENDrequest({
				pack:{
					db:'Company',
					collect:'Employee_Account_Device',
					method:"QUERY",
					options:{
						query:{}
					}
				},
				route:'STORE',
				request:'MART'
			}).then(answer=>{
				if (answer.success) {
					let list = []
					for (let i = 0; i < answer.result.length; i++) {
						for (let j = 0; j < answer.result[i].Account.length; j++) {
							if (answer.result[i].Account[j].type == 'VHP') {
								list.push({
									text:answer.result[i].name,
									value:answer.result[i].Account[j].user
								})
							}
						}
						
					}
					this.setState({
						userList:list
					})
				}
				
			})
		}
		
	}

	UpdateTracker(data) {
		let quotes = Clone(this.state.quotes)
		let selectedLead = -1;
		for (let i = 0; i < quotes.length; i++) {
			if (quotes[i].id == data.id) {
				selectedLead = i
			}
		}
		if (selectedLead > -1) {
			console.log("UPDATE:::::",data)
			for (let key in data) {
				if (quotes[selectedLead][key] !== undefined) {
					if (data[key].text != undefined) {
						quotes[selectedLead][key] = data[key].value
					} else {
						quotes[selectedLead][key] = data[key]
					}
				}
			}
			if (data.prstdate != null) {
				quotes[selectedLead].date = data.prstdate
			}
			if (this.props.settings.dev == true) {
				this.setState({
					quotes:quotes
				})
			} else {
				//if(this.state.quotes[this.state.selectedLead].trackid){
				this.state.Mongo.SENDrequest({
					pack:{
						db:'Replacement',
						collect:'Tracking350',
						method:"UPDATE",
						options:{
							query:{id:this.state.quotes[selectedLead].id},
							update:{...quotes[selectedLead]}
						}
					},
					route:'STORE',
					request:'MART'
				}).then(answer=>{
					if (answer.success) {
						this.SetDropNote({
							showNote:true,
							level:"green",
							text:"Tracker updated",
							timeout:0
						})
						this.SetQuotes(quotes)
					} else {
						this.SetDropNote({
							showNote:true,
							level:"red",
							text:"Failed to update",
							timeout:0
						})
					}
					//console.log("MONGO UPDATE RESPONSE	", answer)
					
				})
			}
		}
		
	}

	/**
	 * Updates quotes without pushing to API
	 * @param {Array} quotes | an array containing quotes
	 */
	SetQuotes(quotes) {
		this.setState({
			quotes:quotes
		})
	}

	GetFilterCardData() {
		if (this.props.user.name == 'RESMAN') {
			return [
				{
					title:"Consultant",
					type:"DropDown",
					key:"estimator",
					list:this.state.userList
				},
				{
					title:"Status",
					type:"DropDown",
					key:"status",
					list:this.state.statusDrops
				},
				{
					title:"Stage",
					type:"DropDown",
					key:"stage",
					list:[
						{text:"Job", value:"job"},
						{text:"Quote", value:"quote"}
					]
				},
				{
					title:"Date",
					type:"DateRange",
					key:"date"
				},
				{
					title:"Amount",
					type:"NumRange",
					key:"amount"
				},
				{
					title:"Has Bids",
					type:"Length",
					key:"bids"
				}
			]
		}
	
		return [
			{
				title:"Status",
				type:"DropDown",
				key:"status",
				list:this.state.statusDrops
			},
			{
				title:"Stage",
				type:"DropDown",
				key:"stage",
				list:[
					{text:"Job", value:"job"},
					{text:"Quote", value:"quote"}
				]
			},
			{
				title:"Date",
				type:"DateRange",
				key:"date"
			},
			{
				title:"Amount",
				type:"NumRange",
				key:"amount"
			},
			{
				title:"Has Bids",
				type:"Length",
				key:"bids"
			}
		]
	}
    
	GetApp() {
		return <ResidentialTracker 
			settings = {this.state.settings}
			quotes = {FilterDataShallow(this.state.quotes, this.state.filters)}
			UpdateTracker = {this.UpdateTracker}
			SetQuotes = {this.SetQuotes}
			SetDropNote = {this.SetDropNote}
			user = {this.props.user}
			LogUserOut = {this.props.LogUserOut}
			ToggleBoolean = {this.ToggleBoolean}
			statusDrops = {this.state.statusDrops}
		/>
	}

	RequestHelp() {
		if (window.ipcRenderer != undefined) {
			window.ipcRenderer.invoke(routeNames.getVerInfo).then(resp=>{
				if (resp.success) {
					let app = resp.appinfo.name + ' - RRTracker';
					let version = resp.appinfo.version;
					let email = 'iamsupport@vogelheating.com';
					let subject = `Support Request - ${app}`;
					let body = `Application: ${app} - ${version}: ${tabs[this.state.currentTab]}`;
					window.location.assign(`mailto:${email}?subject=${subject}&body=${body}`);
				}
			})
		} else {
			let app = 'RRTracker';
			let version = 'Web';
			let email = 'iamsupport@vogelheating.com';
			let subject = `Support Request - ${app}`;
			let body = `Application: ${app} - ${version}: ${tabs[this.state.currentTab]}`;
			window.location.assign(`mailto:${email}?subject=${subject}&body=${body}`);
		}
	}

    render() {
        return(
            <>
				{this.GetApp()}
				<DropNote 
					{...this.state.DropNoteProps}
					CloseNote = {this.CloseNote}
				/>
				<BasicLoadingScreen 
					text = {this.state.loadingMsg}
					loading = {this.state.loading}
				/>
				{this.state.filterActive&&<FloatContainer>
					<DataFilterCard 
						SetFilters = {this.SetFilters}
						ResetFilters = {this.ResetFilters}
						CloseFunction = {this.ToggleBoolean}
						closeData = "filterActive"
						filters = {this.state.filters}
						data = {this.GetFilterCardData()}
						title = "Tracker"
					/>
				</FloatContainer>}
            </>
        );
    }
}