import React, { Component } from 'react';
import { BasicHeader, Card, CardContent, Checkbox, Clone, DropDown, FloatContainer, FormItem, GetDropDownFromValue, ImageButton, InputForm, SetProperty, TextButton, TextInput, ToggleBoolean, priceformat } from 'vhp-component-library';

import './Styles/TrackerEditor.css'
import { Pie, PieChart, ResponsiveContainer, Cell, Legend, Tooltip } from 'recharts';

import { formatter } from '../bin/vg-gentools';
import { CalculateBidAmount, GetBidData, GetSoldBidTotal, GetUnsoldBidTotal, abid } from '../bin/tracker-tools';
import { BasicDialogue } from 'vhp-forms-library';
import { BidTableRow } from './BidTableRow';

import { PopoutForm } from 'vhp-component-library';

let CategoryList = [];
let SaleTypeList = [];
let TimeRanList = [];
let CompanyList = [];
let LeadSourceList = [];
let PresentedViaList = [];
let ProgressList = [];

function SetDroplists(settings) {
	let rsets = settings.reporting;
	CategoryList = [];
	SaleTypeList = [];
	TimeRanList = [];
	CompanyList = [];
	LeadSourceList = [];
	PresentedViaList = [];
	ProgressList = [];
	
	for(let ea in rsets.cat){  // Categories
		CategoryList.push({
			text: rsets.cat[ea].descr,
			value: ea
		})
	}

	for(let ea in rsets.source){  // Leads & Sources
		LeadSourceList.push({
			text: rsets.source[ea],
			value: ea
		})
	}
	
	for(let ea in rsets.comp){  // Companies
		CompanyList.push({
			text: rsets.comp[ea],
			value: ea
		})
	}
	
	for(let ea in rsets.time){  // Times
		TimeRanList.push({
			text: rsets.time[ea],
			value: ea
		})
	}

	for(let ea in rsets.prstvia){  // Presentation Types
		PresentedViaList.push({
			text: rsets.prstvia[ea],
			value: ea
		})
	}

	for(let ea in rsets.saletype){  // Sale Types
		SaleTypeList.push({
			text: rsets.saletype[ea],
			value: ea
		})
	}

	for(let ea in rsets.progress){  // Sale Types
		ProgressList.push({
			text: rsets.progress[ea],
			value: ea
		})
	}

	let DropDownKeys = {
		source:LeadSourceList,
		time:TimeRanList,
		prstvia:PresentedViaList,
		cat:CategoryList,
		comp:CompanyList,
		lead:LeadSourceList,
		saletype:SaleTypeList,
		progress:ProgressList
	}

	return DropDownKeys
}

const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042'];

/**
 * Starter class for an application. Contains a set of containers with a MenuBar allowing you to swap between tabs.
 * MenuBar style can be customized, and containers can be added to the Container to add pages to your app
 * Clone and modify this file whenever you want to create a new app that can sit inside App.js or the dock.
 */
export class TrackerEditor extends Component {
    constructor(props) {
        super(props)

		this.state = {
			id:"",
			ref:"",
			projectname:"",
			client:"",
			street:"",
			city:"",
			zip:"",
			phone:"",
			email:"",
			stage:"",
			status:"",
			date:"",
			ref:"",
			unit:"",
			solddate:"",
			time:{text:"Select One", value:""},
			source:{text:"Select One", value:""},
			lead:{text:"Select One", value:""},
			rewards:false,
			prstdate:"",
			prstvia:{text:"Select One", value:""},
			bookprc:false,
			bid:{
				sold:false,
				cat:"",
				amount:0,
				category:{text:"Select one", value:null},
				progress:{text:"Select one", value:null},
				comp:{text:"VHC", value:"VHC"},
				saletype:{text:"Select one", value:null},
				customerTotal:0,
				ameren:0,
				manfRebate:0,
				specialRebate:0
			},
			DropDownKeys:{
				source:LeadSourceList,
				time:TimeRanList,
				prstvia:PresentedViaList,
				cat:CategoryList,
				comp:CompanyList,
				lead:LeadSourceList,
				saletype:SaleTypeList,
				progress:ProgressList
			},
			confirmTimeDialog:false
		}

		this.SetProperty = SetProperty.bind(this)
		this.SetBidProperty = this.SetBidProperty.bind(this)
		this.GetBids = this.GetBids.bind(this)
		this.UpdateFunction = this.UpdateFunction.bind(this)
		this.SetDate = this.SetDate.bind(this)
		this.ToggleBoolean = ToggleBoolean.bind(this)
    }

	componentDidMount() {
		if (this.props.data != undefined) {
			let state = Clone(this.state)
			for (let key in state) {
				if (this.props.data[key] != undefined) {
					if (this.state.DropDownKeys[key] != undefined) {
						if (this.props.data[key] !== "") {
							state[key] = {
								text: this.props.data[key],
								value: this.props.data[key]
							}
						}
					} else {
						if (key == 'date' || key == 'prstdate') {
							state[key] = this.props.data[key].substring(0, 16);
						} else {
							state[key] = this.props.data[key]
						}
					}
				}
			}
			this.setState({
				...state
			})
		}

		this.setState({
			DropDownKeys:SetDroplists(this.props.settings)
		})
	}
	
	SetBidProperty(value, key) {
		let bid = Clone(this.state.bid)
		if (key == 'amount') {
			bid[key] = CalculateBidAmount(value)
			for (let innerkey in value) {
				bid[innerkey] = value[innerkey]
			}
		} else {
			bid[key] = value
		}
		this.setState({
			bid:bid
		})
	}

	GetBids() {
		let bids = []
		for (let i = 0; i < this.props.data.bids.length; i++) {
			bids.push(<div className='tablerow bid-tablerow'>
				<div className='row-item'>{this.props.data.bids[i].comp}</div>
				<DropDown 
					selected = {GetDropDownFromValue(this.props.data.bids[i].saletype, SaleTypeList)}
					list = {SaleTypeList}
					ChangeFunction = {this.props.EditBid}
					data = {{_id:this.props.data._id, key:"saletype", index:i}}
				/>
				<DropDown 
					selected = {GetDropDownFromValue(this.props.data.bids[i].cat, CategoryList)}
					list = {CategoryList}
					ChangeFunction = {this.props.EditBid}
					data = {{_id:this.props.data._id, key:"cat", index:i}}
				/>
				<DropDown 
					selected = {GetDropDownFromValue(this.props.data.bids[i].progress, ProgressList)}
					list = {ProgressList}
					ChangeFunction = {this.props.EditBid}
					data = {{_id:this.props.data._id, key:"progress", index:i}}
				/>
				{/*<TextInput 
					value = {this.props.data.bids[i].amount}
					ChangeFunction = {this.props.EditBid}
					data = {{_id:this.props.data._id, key:"amount", index:i}}
					type = "number"
				/>*/}
				<PopoutForm
					SaveFunction = {this.props.EditBid}
					formdata = {[
						{
							data:"customerTotal",
							value:this.props.data.bids[i].customerTotal,
							inputType:"TextInput",
							formItemClass:"form-item-basic",
							title:"Customer Total",
							type:'number'
						},
						{
							data:"ameren",
							value:this.props.data.bids[i].ameren,
							inputType:"TextInput",
							formItemClass:"form-item-basic",
							title:"Ameren",
							type:'number'
						},
						{
							data:"manfRebate",
							value:this.props.data.bids[i].manfRebate,
							inputType:"TextInput",
							formItemClass:"form-item-basic",
							title:"Manf. Rebate",
							type:'number'
						},
						{
							data:"specialRebate",
							value:this.props.data.bids[i].specialRebate,
							inputType:"TextInput",
							formItemClass:"form-item-basic",
							title:"Special Rebate",
							type:'number'
						}
					]}
					data = {{_id:this.props.data._id, key:"amount", index:i}}
					text = {priceformat(this.props.data.bids[i].amount)}
					position = 'top'
				/>
				<Checkbox 
					value = {this.props.data.bids[i].sold}
					ClickFunction = {this.props.EditBid}
					data = {{_id:this.props.data._id, key:"sold", index:i}}
				/>
				<ImageButton 
					src = "https://www.vhpportal.com/repo/assets/icons/trash.png"
					ClickFunction = {this.props.RemoveBid}
					data = {{
						_id:this.props.data._id,
						index:i
					}}
				/>
			</div>)
		}
		return bids
	}

	UpdateFunction(data) {
		if (this.state.prstdate == null || this.state.prstdate == "") {
			this.props.SetDropNote({
				showNote:true,
				level:'orange',
				timeout:0,
				text:"Presented date must be filled out to proceed."
			})
		} else {
			let time = this.state.prstdate.split('T')[1]
			time = Number(time.slice(0, 2))
			console.log(time)
			if (data.confirm && (time < 6 || time > 20)) {
				this.ToggleBoolean('confirmTimeDialog')
			} else {
				this.props.UpdateFunction(data.data)
				//sthis.props.CloseFunction(this.props.closeData)
			}
		}
	}

	SetDate(value) {
		this.setState({
			date:value
		})
	}

    render() {
        return(
            <>
				<Card
					title = {"Lead Overview - " + this.state.projectname + " - " + this.state.ref}
					id = "lead-overview-card"
					titlebar = {true}
					actions = {{minimize:null, close:this.props.CloseFunction}}
					data = {{minimize:null, close:this.props.closeData}}
				>
					<CardContent id = "lead-form-cont">
						<div className='lead-basic-info'>
							<BasicHeader 
								text = {this.state.projectname}
								headerClass = "left-align"
								id = "editor-project-name"
							/>
							<FormItem 
								data = {{
									title:"Stage:",
									value:this.state.stage,
									formItemClass:"editor-stage"
								}}
							/>
							<div id = "editor-status">{this.state.status}</div>
							<div id = "editor-contact-info">
								<div id = "editor-name">{this.state.client}</div>
								<div id = "editor-phone">{this.state.phone||"No phone provided"}</div>
								<div id = "editor-email">{this.state.email||"No email provided"}</div>
								<div id = "editor-address">{
									this.state.street + 
									" " + this.state.unit
								}</div>
								<div id = "editor-cityzip">{
									this.state.city +
									", " + this.state.zip
								}</div>
							</div>
						</div>
						<div className='lead-edit-form'>
							<InputForm 
								formdata = {[
									{
										title: "Lead Source",
										data: "source",
										inputType: "DropDown",
										list: LeadSourceList,
										selected: this.state.source,
										searchable: true,
										canClear: true,
										ChangeFunction:this.SetProperty
									},
									{
										title: "Lead Generator",
										data: "lead",
										inputType: "DropDown",
										list: LeadSourceList,
										selected: this.state.lead,
										searchable: true,
										canClear: true,
										ChangeFunction:this.SetProperty
									},
									{
										title: "Rewards",
										data: "rewards",
										inputType: "Checkbox",
										value: this.state.rewards,
										ClickFunction:this.SetProperty
									},
									{
										title: "Appointment Date",
										data: "prstdate",
										inputType: "TextInput",
										type: "datetime-local",
										value: this.state.prstdate,
										ChangeFunction:this.SetProperty
									},
									{
										title: "Sold Date",
										data: "solddate",
										inputType: "TextInput",
										type: "date",
										value: this.state.solddate,
										ChangeFunction:this.SetProperty
									},
									{
										title: "Presented Via",
										data: "prstvia",
										inputType: "DropDown",
										list: PresentedViaList,
										selected: GetDropDownFromValue(this.state.prstvia.value, PresentedViaList),
										searchable: true,
										canClear: true,
										ChangeFunction:this.SetProperty
									},
									{
										title: "Book Price",
										data: "bookprc",
										inputType: "Checkbox",
										value: this.state.bookprc,
										ClickFunction:this.SetProperty
									}
								]}
							/>
						</div>
						<div className='lead-bid-chart'>
							<div className='bid-summary-info'>
								<FormItem 
									data = {{
										title:"Total Bids",
										value:priceformat(GetSoldBidTotal(this.props.data.bids) + GetUnsoldBidTotal(this.props.data.bids)),
										formItemClass:"dollar-sign"
									}}
								/>
								<FormItem 
									data = {{
										title:"Amount Sold",
										value:priceformat(GetSoldBidTotal(this.props.data.bids)),
										formItemClass:"dollar-sign"
									}}
								/>
								<FormItem 
									data = {{
										title:"Amount Unsold",
										value:priceformat(GetUnsoldBidTotal(this.props.data.bids)),
										formItemClass:"dollar-sign"
									}}
								/>
							</div>
							<ResponsiveContainer width="100%" height="100%">
								<PieChart>
									<Pie 
										data = {GetBidData(this.props.data.bids)}
										dataKey="amount"
										nameKey="cat"
										cx="50%"
										cy="50%"
										outerRadius={65}
										innerRadius={40}
										fill='#8884d8'
										title = "Bids"
										label = {formatter}
									>
										{GetBidData(this.props.data.bids).map((entry, index) => (
											<Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
										))}
									</Pie>
									<Legend 
										align = "left"
										layout='vertical'
									/>
									<Tooltip />
								</PieChart>
							</ResponsiveContainer>
						</div>
					</CardContent>
					<CardContent cardContentClass = "bid-cont no-padding">
						<CardContent cardContentClass = "bid-table-cont no-padding">
							<div className='action-row add-bid-row left'>
								<FormItem 
									data = {{
										selected:this.state.bid.comp,
										list:CompanyList,
										data:"comp",
										ChangeFunction:this.SetBidProperty,
										inputType:"DropDown",
										id:"bid-dept-form-item"
									}}
								/>
								<FormItem 
									data = {{
										selected:this.state.bid.saletype,
										list:SaleTypeList,
										data:"saletype",
										ChangeFunction:this.SetBidProperty,
										inputType:"DropDown",
										id:"bid-cat-form-item"
									}}
								/>
								<FormItem 
									data = {{
										selected:this.state.bid.category,
										list:CategoryList,
										data:"category",
										ChangeFunction:this.SetBidProperty,
										inputType:"DropDown",
										id:"bid-cat-form-item"
									}}
								/>
								<FormItem 
									data = {{
										selected:this.state.bid.progress,
										list:ProgressList,
										data:"progress",
										ChangeFunction:this.SetBidProperty,
										inputType:"DropDown",
										id:"bid-cat-form-item"
									}}
								/>
								<PopoutForm
									SaveFunction = {this.SetBidProperty}
									formdata = {[
										{
											data:"customerTotal",
											value:this.state.bid.customerTotal,
											inputType:"TextInput",
											formItemClass:"form-item-basic",
											title:"Customer Total",
											type:'number'
										},
										{
											data:"ameren",
											value:this.state.bid.ameren,
											inputType:"TextInput",
											formItemClass:"form-item-basic",
											title:"Ameren",
											type:'number'
										},
										{
											data:"manfRebate",
											value:this.state.bid.manfRebate,
											inputType:"TextInput",
											formItemClass:"form-item-basic",
											title:"Manf. Rebate",
											type:'number'
										},
										{
											data:"specialRebate",
											value:this.state.bid.specialRebate,
											inputType:"TextInput",
											formItemClass:"form-item-basic",
											title:"Special Rebate",
											type:'number'
										}
									]}
									data = 'amount'
									text = {priceformat(this.state.bid.amount)}
									position = 'top'
								/>
								<FormItem 
									data = {{
										value:this.state.bid.sold,
										data:"sold",
										ClickFunction:this.SetBidProperty,
										inputType:"Checkbox",
										id:"bid-sold-form-item"
									}}
								/>
								<ImageButton 
									ClickFunction = {this.props.AddBid}
									src = "https://www.vhpportal.com/repo/assets/icons/add.png"
									buttonClass = "icon-button"
									data = {{id:this.props.data._id, bid:abid(this.state.bid)}}
								/>
							</div>
							<div className='tablerow header' id = "bid-table-header">
								<div className='row-item'>Dept</div>
								<div className='row-item'>Sale Type</div>
								<div className='row-item'>Category</div>
								<div className='row-item'>Progress</div>
								<div className='row-item'>Amount</div>
								<div className='row-item'>Invoiced</div>
							</div>
							<CardContent cardContentClass = "bid-table no-padding">
								{this.GetBids()}
							</CardContent>
						</CardContent>
					</CardContent>
					<div className='action-row right'>
						<TextButton 
							text = "Update"
							ClickFunction = {this.UpdateFunction}
							data = {{data:this.state, confirm:true}}
						/>
					</div>
				</Card>
				{this.state.confirmTimeDialog&&<FloatContainer>
					<BasicDialogue 
						title = "Confirmation Required"
						message = "The appointment time falls outside of the expected appointment time range. Appointment time must be between 6AM and 8PM."
						actions = {[
							{
								text:"Close",
								ClickFunction:this.ToggleBoolean,
								data:"confirmTimeDialog",
								buttonClass:"exit-dialogue-cancel"
							}
						]}
					/>
				</FloatContainer>}
			</>
        );
    }
}