import React, { Component } from 'react';
import { ActionRow, Card } from 'vhp-component-library';

export class BasicDialogue extends Component {
    constructor(props) {
        super(props)
    }

    render() {
        return(
            <Card
				cardClass = "exit-dialogue-card"
				titlebar = {this.props.title}
				title = {this.props.title}
				id = {this.props.id}
				actions = {{close:this.props.CloseFunction, minimize:null}}
				data = {{close:this.props.closeData, minimize:null}}
			>
				<div className='exit-message'>{this.props.message}</div>
				{this.props.actions&&<ActionRow 
					data = {this.props.actions}
				/>}
			</Card>
        );
    }
}